import { useEffect, useState, useLayoutEffect } from "react";

const quotes = [
    'Podre to jedyna rzecz na ktr wydajemy pienidze, a stajemy si bogatsi.',
    'Podrowanie uczy skromnoci. Widzisz, jak niewiele miejsca zajmujesz w wiecie.',
    'Kade miejsce, ktre odwiedzasz staje si w jaki sposb czci ciebie.',
    'Nie czekaj. Pora nigdy nie bdzie idealna.',
    'Jeli mylisz, e przygody bywaj niebezpieczne, sprbuj rutyny. Ona jest miercionona.',
    "Podrnik, ktry nie potrafi obserwowa, jest jak ptak pozbawiony skrzyde.",
    "Lepiej zobaczy co raz, ni sysze o tym tysic razy."
]

const styles = {
    position: 'absolute',
    padding: '10px',
    top: '10px',
    left: 0,
    right: 0,
    textAlign: 'center',
    color: '#fff',
    fontStyle: 'italic'
}

function InspiringQuote(props) {

    const [quote, setQuote] = useState('Wczytywanie cytatu...');
    const [loading, setLoading] = useState(true);

    useEffect(() => {
        // ... pobieranie
        setLoading(false);
    }, []);

    useLayoutEffect(() => {
        setQuote(quotes[Math.floor(Math.random() * quotes.length)]);
    }, [loading])

    return (
        <p style={styles}>{quote}</p>
    );
}

export default InspiringQuote;